import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';

import AttachFileIcon from '@mui/icons-material/AttachFile';
import Paper from '@mui/material/Paper';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import SendIcon from '@mui/icons-material/Send';
import Typography from '@mui/material/Typography';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import DownloadIcon from '@mui/icons-material/Download';
import Chip from '@mui/material/Chip';
import { Link } from 'react-router-dom'
import InputBase from '@mui/material/InputBase';
import Moment from 'react-moment';
import { styled } from '@mui/material/styles';
import Badge from '@mui/material/Badge';
import "react-perfect-scrollbar/dist/css/styles.css";
import PerfectScrollbar from 'react-perfect-scrollbar'

import FileViewer from "react-file-viewer";

let ps;

export default function ChatTextBox({ sendSocketHandler, messagesData, userId=[], selectFileHandler, downlodeHandler, currentMessage=[], onlineUsers=[], receiver={} }) {
    const [valueTemp, setValueTemp] = React.useState('');
    const [scrollEl, setScrollEl] = React.useState();
    const ref = React.useRef(null);

    const listRef = React.useRef();
    const ps = React.useRef();

       


    React.useEffect(() => {
        const curr = ps.current
        if (curr) {
          curr.scrollTop = curr.scrollHeight
        }
      }, [messagesData]);


    const onChangeHandler = (e) => {
        const { value } = e.target
        setValueTemp(value)
    }
    const handleKeyDown = (e) => {
        if (e.keyCode === 13 && e.shiftKey) {
            return;
        } else if (e.keyCode === 13) {
            e.preventDefault();
            sendSocketHandler(valueTemp);
            setValueTemp('');
        }
    }
    const onErrorHandler = (e) => {
        console.log(e, "error in file-viewer");
    }
    const StyledBadge = styled(Badge)(({ theme }) => ({
        '& .MuiBadge-badge': {
            backgroundColor: '#44b700',
            color: '#44b700',
            boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
            '&::after': {
                position: 'absolute',
                top: 0,
                left: 0,
                width: '100%',
                height: '100%',
                borderRadius: '50%',
                animation: 'ripple 1.2s infinite ease-in-out',
                border: '1px solid currentColor',
                content: '""',
            },
        },
        '@keyframes ripple': {
            '0%': {
                transform: 'scale(.8)',
                opacity: 1,
            },
            '100%': {
                transform: 'scale(2.4)',
                opacity: 0,
            },
        },
    }));


    return (
        receiver && <Box >
            <List sx={{ backgroundColor: '#dfb7eb', marginTop: 2, borderRadius: 4.3, padding: 0 }}>
                <ListItem alignItems="flex-start">
                    {onlineUsers.includes(receiver._id) ? <ListItemAvatar><StyledBadge
                        overlap="circular"
                        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                        variant="dot"
                    >
                        <Avatar alt={receiver.name} src={receiver.image} />
                    </StyledBadge>
                    </ListItemAvatar>
                        :
                        <ListItemAvatar>
                            <Avatar
                                alt={receiver.name}
                                src={receiver.image}
                            />
                        </ListItemAvatar>
                    }
                    <ListItemText primary={`${receiver.name}`} secondary={onlineUsers.includes(receiver._id) ? 'Online' : 'Offline'} />
                </ListItem>
            </List>
            <Box sx={{ backgroundColor: '#f6f5ff' }} ref={ref}>
                <CssBaseline />

                <List sx={{height: '70vh'}}>
                <PerfectScrollbar containerRef={el => (ps.current = el)} >
                    {messagesData.map((data, index) => {
                        return (
                            <div key={data._id} >
                                {data.sender?._id !== userId ?
                                    <div key={index + 1} style={{ backgroundColor: '#afb1db', borderRadius: 20, margin: 20, width: '45%',marginTop: 2, position: 'relative', float: 'left' }}>
                                        {data.text && <ListItem button sx={{ padding: 0.5, paddingLeft: 2.5 }}>
                                            <ListItemText primary={data.text} sx={{ color: 'white' }} />
                                        </ListItem>
                                        }


                                        {data.file &&
                                            data.file.length ? (data.file).map((ele, i) => {
                                                const filetyp = ele.split('.').pop();
                                                return <ListItem sx={{ color: 'white', position: 'relative' }}>
                                                    <div key={i} >
                                                        {/.*\.(gif|jpe?g|bmp|png)$/igm.test(ele)
                                                            ? <img src={ele} alt="testImage" style={{ width: '100%', borderRadius: 5 }} />
                                                            : <div><FileViewer fileType={filetyp} filePath={ele} onError={onErrorHandler} /></div>
                                                        }

                                                        <Chip
                                                            sx={{ color: 'white', padding: 0 }}
                                                            label="Download"
                                                            color="secondary"
                                                            onClick={() => downlodeHandler(ele)}
                                                            onDelete={() => downlodeHandler(ele)}
                                                            deleteIcon={<DownloadIcon />}
                                                        />

                                                    </div>
                                                </ListItem>
                                            }) : ''
                                        }
                                        <Moment style={{ marginLeft: 16, position: 'absolute', fontSize: 12, color: '#969696', bottom: -18 }} calendar >{data.createdAt}</Moment>
                                    </div>
                                    :
                                    <div key={index + 1} style={{ backgroundColor: '#6369ff', borderRadius: 20, margin: 20, width: '45%', float: 'right', marginTop: 2, position: 'relative' }}>
                                        {data.text && <ListItem button sx={{ textAlign: 'right',padding: 0.5,paddingRight: '20px !important', width: '100%' }}>
                                            <ListItemText primary={data.text} sx={{ color: 'white', width: '100%' }} />
                                        </ListItem>
                                        }


                                        {data.file &&
                                            data.file.length ? (data.file).map((ele, i) => {
                                                const filetyp = ele.split('.').pop();
                                                return <ListItem sx={{ float: 'right', color: 'white', position: 'relative' }}>
                                                    <div key={i} >
                                                        {/.*\.(gif|jpe?g|bmp|png)$/igm.test(ele)
                                                            ? <img src={ele} alt="testImage" style={{ width: '100%', borderRadius: 5 }} />
                                                            : <div style={{ marginLeft: 180 }}><FileViewer fileType={filetyp} filePath={ele} onError={onErrorHandler} /></div>
                                                        }
                                                        <Chip
                                                            sx={{ float: 'right', color: 'white', padding: 0 }}
                                                            label="Download"
                                                            color="secondary"
                                                            onClick={() => downlodeHandler(ele)}
                                                            onDelete={() => downlodeHandler(ele)}
                                                            deleteIcon={<DownloadIcon />}
                                                        />
                                                    </div>
                                                </ListItem>
                                            }) : ''
                                        }
                                        <Moment style={{ marginLeft: 20, position: 'absolute', fontSize: 12, color: '#969696', bottom: -18 }} calendar >{data.createdAt}</Moment>
                                    </div>
                                }
                            </div>
                        )
                    })}
                    </PerfectScrollbar>
                </List>
            </Box>
            <Paper sx={{ display: 'flex', alignItems: 'center', borderRadius: 10 }} elevation={3}>
                <IconButton color="primary" onClick={selectFileHandler} aria-label="directions" sx={{ p: 2, transform: 'rotate(45deg)' }}>
                    <AttachFileIcon color='secondary' />
                </IconButton>
                <InputBase
                    id="outlined-multiline-flexible"
                    placeholder='Type your messge here...'

                    multiline
                    fullWidth
                    maxRows={4}
                    value={valueTemp}
                    onChange={onChangeHandler}
                    onKeyDown={handleKeyDown}
                />
                <IconButton onClick={() => { sendSocketHandler(valueTemp), setValueTemp('') }} sx={{ mr: 1 }} aria-label="directions">
                    <SendIcon color='secondary' />
                </IconButton>
            </Paper>

        </Box>
    );
}
