import React, { useEffect, useState, useContext } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import Moment from 'react-moment';
import moment from 'moment'
import * as Yup from 'yup';
import io from "socket.io-client";


import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import DatePicker from 'components/DatePicker'
import DilogeBox from 'components/DilogeBox'
import PdfDocument from 'components/PdfDocument'
import NormalSelect from 'components/NormalSelect'
import { useFileUpload } from 'use-file-upload'
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box'

import CustomTable from "components/CustumTabal";

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';


import dummyImg from "assets/img/dummy/male_user.webp";
import { string } from "prop-types";

import { title } from "assets/jss/material-dashboard-react.js";
import { useParams } from "react-router";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import ChatSidebar from "components/Chats/ChatSidebar.js";
import ChatTextBox from 'components/Chats/ChatTextBox'
import ImageList from '@mui/material/ImageList';
import { ToastContext } from "index";
import ImageListItem from '@mui/material/ImageListItem';
import ImageModel from 'components/Chats/ImageModel';
import { socket } from "layouts/Admin.js";
import Admin from "layouts/Admin.js";

import { LoginContext } from "index.js";
import { CustumContex } from 'layouts/Admin';
import { TitleContex } from 'layouts/Admin';


import { HOST } from "config.js";
import { Helmet } from "react-helmet";




const styles = {
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0",
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
    },
};

const useStyles = makeStyles(styles);

export default function ChatAdmin() {


    const { setToastData } = React.useContext(ToastContext);


    const [messages, setMessages] = React.useState([])
    const [messageUser, setMessageUser] = React.useState([]);
    const [currentMessage, setCurrnetMessage] = React.useState([]);

    const classes = useStyles();
    const [files, selectFiles] = useFileUpload();
    const [modelToggle, setModelToggle] = useState(false);
    const { userData } = React.useContext(LoginContext);
    const { receiver, setReceiver, currentMessageUser, onlineUsers } = useContext(CustumContex);
    const { title } = useContext(TitleContex);
    const [searchData, setSearchData] = useState([]);



    React.useEffect(() => {
        const tempMessageUser = messageUser;
        const index = messageUser.findIndex(ele => ele._id === receiver._id);
        if (receiver?._id && userData._id) {
            joinChatHandler(receiver._id);
            messageSeenHandler(receiver._id);
            tempMessageUser[index] = { ...messageUser[index], unseenMessages: 0 };
            setMessageUser(tempMessageUser);
        }

    }, [receiver]);


    React.useEffect(() => {
        fetch(`${HOST}/api/user/messages/${userData._id}`)
            .then(res => res.json())
            .then(res => {
                if (res.data) {
                    setMessageUser(res.data);
                }
            })
    }, [])

    React.useEffect(() => {
        socket.on("message", (data) => {
            setCurrnetMessage(data);
        });
    }, [socket]);

    React.useEffect(() => {
        if (receiver._id === currentMessage._id || currentMessage._id === userData._id) {
            setMessages((pre) => [...pre, currentMessage])
        }
    }, [currentMessage])

    useEffect(() => {
        if (currentMessageUser._id) {
            const findData = messageUser.find((ele) => ele._id === currentMessageUser._id)
            if (findData) {
                const filter1 = messageUser.filter(ele => ele._id !== currentMessageUser._id)
                setMessageUser([{ _id: findData._id, name: findData.name, image: findData.image, unseenMessages: findData.unseenMessages + 1 }, ...filter1]);
            } else {
                setMessageUser([...messageUser, { ...currentMessageUser, unseenMessages: 0 }]);
            }
        }
        return () => setMessageUser([]);
    }, [currentMessageUser])


    console.log("messagesUser", messageUser);
    console.log('messages', messages);
    console.log('receiver', receiver);




    const sendSocketHandler = (value) => {
        if (value !== '') {
            const valu = { text: value, sender: userData, _id: receiver._id, receiver: receiver._id }
            socket.emit("chat", valu);
        }
    }
    const listClickHandler = (value) => {
        console.log('value', value);
        setReceiver(value);
        setMessages([]);
        joinChatHandler(value._id);
    }

    const joinChatHandler = async (value) => {
        await fetch(`${HOST}/api/message?sender=${userData._id}&receiver=${value}`)
            .then(res => res.json())
            .then(res => {
                console.log(res, 'inner message');
                if (res.data) {
                    const msgData = [];
                    (res.data).forEach(ele => {
                        msgData.unshift({ _id: ele._id, name: ele.sender.name, sender: ele.sender, text: ele.message, file: ele.file, createdAt: ele.createdAt })
                    });
                    setMessages(msgData);
                }
            })
        socket.emit('joinChat', { sender: userData, _id: value, receiver: value })
    }



    const selectFileHandler = () => {
        selectFiles({ multiple: true }, (files) => {
            files.map(({ source, name, size, file }) => {
                console.log({ source, name, size, file })
            })
            setModelToggle(true);
        })
    }
    const sendfileHandler = (value) => {
        console.log('send Clicked');
        setModelToggle(false);
        const reader = new FileReader();
        reader.readAsDataURL(files[0].file);
        reader.onloadend = function (e) {
            socket.emit('chat', { text: value, sender: userData, _id: receiver._id, receiver: receiver._id, file: reader.result });
        };
    }
    const messageSeenHandler = (id) => {
        fetch(`${HOST}/api/message/seen/${id}`);
    }

    const downlodeHandler = (value) => {
        fetch(value, {
            method: "GET",
            headers: {}
        })
            .then(response => {
                response.arrayBuffer().then(function (buffer) {
                    const url = window.URL.createObjectURL(new Blob([buffer]));
                    const link = document.createElement("a");
                    link.href = url;
                    link.setAttribute("download", value.substr(value.lastIndexOf('/'))); //or any other extension
                    document.body.appendChild(link);
                    link.click();
                });
            })
            .catch(err => {
                console.log(err);
            });
    }

    const searchInputHandler = (e) => {
        const search = e.target.value;
        if (!search) return setSearchData([]);
        fetch(`${HOST}/api/user/name/${search}`)
            .then(res => res.json())
            .then(res => {
                if (res.data) {
                    setSearchData(res.data);
                }
            })
    }
    const searchDataClickHandler = (val) => {
        setReceiver(val);
        setMessages([]);
        setSearchData([]);
        joinChatHandler(val);
    }

    return (
        <div>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>{title}</title>
                </Helmet>
                    <GridContainer >
                        <GridItem xs={12} sm={12} md={12}>
                            <div style={{ backgroundColor: '#f6f5ff' }}>
                                <GridContainer xs={12} md={12}>
                                    <GridItem xs={4} md={4}>
                                        <ChatSidebar
                                            messagesUser={messageUser}
                                            listClickHandler={listClickHandler}
                                            receiver={receiver}
                                            searchInputHandler={searchInputHandler}
                                            searchData={searchData}
                                            searchDataClickHandler={searchDataClickHandler}
                                            onlineUsers={onlineUsers}
                                            userData={userData}
                                        />
                                    </GridItem>
                                    <GridItem xs={12} md={8}>
                                        <ChatTextBox
                                            sendSocketHandler={sendSocketHandler}
                                            messagesData={messages}
                                            selectFileHandler={selectFileHandler}
                                            userId={userData._id}
                                            downlodeHandler={downlodeHandler}
                                            receiver={receiver}
                                            onlineUsers={onlineUsers}
                                        />
                                        <ImageModel files={files} modelToggle={modelToggle} setModelToggle={setModelToggle} sendfileHandler={sendfileHandler} />
                                    </GridItem>
                                </GridContainer>
                            </div>
                        </GridItem>
                    </GridContainer>

        </div>
    );
}

