import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Checkbox from '@mui/material/Checkbox';
import Avatar from '@mui/material/Avatar';
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import Badge from '@mui/material/Badge';
import MailIcon from '@mui/icons-material/Mail';
import { styled } from '@mui/material/styles';
import "react-perfect-scrollbar/dist/css/styles.css";
import PerfectScrollbar from 'react-perfect-scrollbar'




export default function ChatSidebar({ messagesUser, listClickHandler, receiver, searchInputHandler, searchData, searchDataClickHandler, onlineUsers=[], userData={} }) {
  

  const StyledBadge = styled(Badge)(({ theme }) => ({
    '& .MuiBadge-badge': {
      backgroundColor: '#44b700',
      color: '#44b700',
      boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
      '&::after': {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        borderRadius: '50%',
        animation: 'ripple 1.2s infinite ease-in-out',
        border: '1px solid currentColor',
        content: '""',
      },
    },
    '@keyframes ripple': {
      '0%': {
        transform: 'scale(.8)',
        opacity: 1,
      },
      '100%': {
        transform: 'scale(2.4)',
        opacity: 0,
      },
    },
  }));


  return (
    <div style={{ width: '100%', maxWidth: 360, backgroundColor: '#f8e3ff', margin: 16, borderRadius: 20 }}>
     
      <List sx={{ marginTop: 2, borderRadius: 4.3, padding: 0 }}>
        <ListItem alignItems="flex-start">
          <ListItemAvatar>
            <Avatar alt={userData.name} src={userData.image} />
          </ListItemAvatar>
          <ListItemText
            primary={userData.name}
          />
        </ListItem>

        <Paper
          sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', borderRadius: 5, margin: 2, backgroundColor: '#f6f5ff', marginTop: 0 }}
        >
          <InputBase
            sx={{ ml: 1, flex: 1 }}
            placeholder="Search.."
            onChange={searchInputHandler}
            inputProps={{ 'aria-label': 'search google maps' }}
          />
          <IconButton aria-label="search">
            <SearchIcon />
          </IconButton>
        </Paper>
        {searchData.length ? <Box sx={{ maxWidth: 360, bgcolor: '#f6f5ff', borderRadius: 5, margin: 1 }}>
          <nav aria-label="search names">
            <List>
              {searchData.map((el) => (
                <ListItem key={el._id} sx={{ padding: 0 }}>
                  <ListItemButton component="a" href="#" onClick={() => searchDataClickHandler(el)}>
                    <ListItemText primary={el.name} />
                  </ListItemButton>
                </ListItem>
              ))
              }
            </List>
          </nav>
        </Box> : ''
        }
      </List>
      <List >
        <div style={{height: "70vh"}}>
      <PerfectScrollbar >
        {messagesUser.map((value) => {
          const labelId = `checkbox-list-secondary-label-${value._id}`;
          return (
            <ListItem
              key={value._id}
              disablePadding
              onClick={() => listClickHandler(value)}
            >

              <ListItemButton sx={{ backgroundColor: receiver._id === value._id ? 'mediumorchid' : '', borderRadius: 1 }} >
                {onlineUsers.includes(value._id)?<ListItemAvatar> <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar alt={value.name} src={value.image} />
                </StyledBadge>
                </ListItemAvatar>
                  :
                  <ListItemAvatar>
                    <Avatar
                      alt={value.name}
                      src={value.image}
                    />
                  </ListItemAvatar>
                }
                <ListItemText id={labelId} primary={`${value.name}`} />
                <Badge badgeContent={value.unseenMessages} color="secondary" />
              </ListItemButton>
            </ListItem>
          );
        })}
        </PerfectScrollbar>
        </div>
      </List>
    </div>
  );
}